<template>
	<div class="layout">
    <Customer_service></Customer_service>
		<Header class="header">
      <span ><span style="width: 230px;text-align: center;margin: 0 auto;display: inline-block;" @click="changecity">
        <span v-if="site !==undefined">{{site}}</span>
        <span v-if="site ===undefined"></span>
        +</span></span></Header>
		<Content class="content top_p bottom_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="" name="name1" span="8">
						<Row type="flex" :gutter="24">
							<Col span="8" v-for="(item, index) in sb_fl" :key="index">
							<p  @click="product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</Col>
							<Col span="8">
							<p @click="sb_all"><img src="../../assets/sb_all.png"/></p>
							</Col>
						</Row>
					</TabPane>
					<TabPane label="" name="name2">
						<Row type="flex" :gutter="24">
							<div></div>
							<Col span="8" v-for="(item, index) in gjsb" :key="index">
							<p  @click="gj_product_list(item.areaCode)"><img :src=item.src />{{item.name}}</p>
							</Col>
						</Row></TabPane>
					<TabPane label="" name="name3">
						<Row type="flex" :gutter="24">
							<div></div>
							<Col span="8" v-for="(item, index) in mb" :key="index">
							<p  @click="mb_product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</Col>
							<Col span="8">
							<p @click="sb_all_mb()"><img src="../../assets/sb_all.png"/></p>
							</Col>
						</Row></TabPane>
					<TabPane label="" name="name4">
						<Row type="flex" :gutter="16">
							<div></div>
							<Col span="8" v-for="(item, index) in zl_fl" :key="index">
							<p  @click="zl_meau(item.categoryFir,item.value)"><img :src=item.src />{{item.name}}</p>
							</Col>
						</Row>
					</TabPane>
				</Tabs>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<Col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br /></router-link>
				</Col>
				<Col span="6">
				<router-link to="product" class="active"><img src="../../assets/icon_2_hover.png" /><br /></router-link>
				</Col>
				<Col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br /></router-link>
				</Col>
				<Col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br /></a>
				</Col>
			</Row>
		</Footer>
	</div>
</template>

<script>
  import Customer_service from './../public/Customer_service.vue';
	export default {
		name: 'product_list',
		data() {
			return {
				site:this.$route.query.site,
				sb_fl: [{
						categoryFir: '01-',
						name: '01-',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02-',
						name: '02-',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03-',
						name: '03-',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04-',
						name: '04-',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05-',
						name: '05-',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06-',
						name: '06-',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07-',
						name: '07-',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08-',
						name: '08-',
						src: require('../../assets/sb_8.png'),
					}
				],
				gjsb: [
				{
						areaCode: '',
						name: '',
						src: require('../../assets/meiguo.png'),
					},{
						areaCode: '',
						name: '',
						src: require('../../assets/oumeng.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/deguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/faguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/xiangg.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/taiwn.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/hanguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/riben.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/othercon.png'),
					}
				],
				mb: [{
						categoryFir: '01-',
						name: '01-',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02-',
						name: '02-',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03-',
						name: '03-',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04-',
						name: '04-',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05-',
						name: '05-',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06-',
						name: '06-',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07-',
						name: '07-',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08-',
						name: '08-',
						src: require('../../assets/sb_8.png'),
					}
				],
				zl_fl: [
				{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/shxy.png'),
					},
					{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/zyys.png'),
					},
					{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/hxyj.png'),
					},
					{
						categoryFir: '',
						value:'',
						name: '.',
						src: require('../../assets/fzzz.png'),
					},
					{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/gdjz.png'),
					},
					{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/jxgc.png'),
					},
					{
						categoryFir: '',
