					},
					{
						categoryFir: '',
						value:'',
						name: '',
						src: require('../../assets/dianx.png'),
					},
				]
			}
		},
		components: {
      Customer_service
		},
		methods: {

      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '',
          message: h('p', null, [
            h('span', null, ' '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
				})
				}
			},

			mb_product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir,
                    vipFlag:'Y',
                    applyType:'1',
                    areaCode:this.$route.query.site,
                    }
				})
				} else{
					this.$router.push({
					path: '/sb_all',
                    query:{
                    vipFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
				}
			},

			gj_product_list(categoryFir) {
				this.$router.push({
					path: '/sb_list',
                    query:{
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'N',
                    }
				})
			},
			zl_meau(categoryFir,value) {
				this.$router.push({
					path: '/zl_meau',
                    query:{
                    	categoryFir:categoryFir,
                    applyType:'2',
                    value:value,
                    areaCode:this.$route.query.site,
                    }
				})
			},

			sb_all() {
				this.$router.push({
					path: '/sb_all'
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    }
				})
			},
			myvip() {
				let loginName = localStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
      changecity() {
        this.$router.push({
          path: '/changecity'
        })
      },
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		p {
			font-size: 14px;
			color: #000000;
			text-align: center;
			margin: 16px 0 26px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>



