<template>
	<div class="layout" v-model="products">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{products.name}}</Header>
		<Content class="top_p2">
			<img :src="products.bannerUrl" class="img" />
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<Col span="12">
					<p>: <br/> {{products.price}}</p>
					</Col>
					<Col span="12">
					<p>: <br/> {{servicePrice}}</p>
					</Col>
					<Col span="12">
					<p>: <br/>{{products.areaCode}}</p>
					</Col>
					<Col span="12">
					<p>: <br/> {{products.id}}</p>
					</Col>
          <Col span="24">
            <p>: {{products.applyRange}}</p>
          </Col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont">
					<Col span="12">
					<p>: <br/> {{products.firstNoticeNo}}</p>
					</Col>
					<!--<col span="12">
					<p>: <br/> {{products.registerNoticeNo}}</p>
					</col>-->
					<Col span="12">
					<p>: <br/> {{products.categorySec}}</p>
					</Col>
					<!--<col span="12">
					<p>: <br/> {{products.expireTime}}</p>
					</col>-->
					<Col span="12">
					<p>: <br/> {{products.deadlineyear}}</p>
					</Col>
					<Col span="12">
					<p>: <br/> {{products.publicFlag == 'Y' ? '':''}}</p>
					</Col>
					<Button v-if="phonevalue=='true'" type="primary" shape="circle">{{products.customerMobile}}</Button>
				</Row>

				<p class="line"></p>
				<div class="de_cont2 bottom_p">
					<span class="title"></span>
					<span class="smal-title"></span>
					<span class="title2">1.</span>
					<span>
					
				</span>
					<span class="title2">2.</span>
					<span>

					</span>
					<span class="title2">3.</span>
					<span>
					5959
				</span>
					<span class="title2">4.</span>
					<span>
					//
				</span>
					<span class="title2">
						5.</span>
					<span>
					1-3,6-10
				</span>
				</div>
			</div>
		</Content>
		<buyservice :price="price"  :flag="oldflag" ></buyservice>
		<Modal v-model="modal12" title="" @on-ok="ok" @on-cancel="cancel" v-if="products.status===0">
			<Row>
				<Col span="12">
				<Rate show-text v-model="valueText" />
				</Col>
			</Row>
		</Modal>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
	import {
		brandgetById,Collectlist
	} from '../../api/api';
	export default {
		name: 'sb_details',
		data() {
			return {
        phonevalue:localStorage.phonevalue,
				products: [],
				modal12: false,
        oldflag:'',
				valueText: 0,
        price:this.price,
				servicePrice:localStorage.brand_fee,
			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//
			getProducts() {
				this.listLoading = true;
				//NProgress.start();
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
					applyType:applyType
				};
				brandgetById(para).then((res) => {
					//console.log(res);
					this.products = res.data.result;
					this.price = this.products.price;
            this.id = this.products.id;
            let para = {
              customerId: localStorage.customerId,
              actionType:'2',
              contentType:'1'
            };
            Collectlist(para).then((res) => {
              this.sb_list = res.data.result.result.brands;
              let charr = []
              this.sb_list.forEach(value => {
                console.log(value.id)
                if (this.id ===value.id){
                  this.flag = true
                } else {
                  this.flag = false
                }
                charr.push(this.flag)
              })
              if(charr.indexOf(true) !== -1){
                this.oldflag=true
              } else{
                this.oldflag=false
              }
              console.log(this.oldflag)
					this.listLoading = false;
            })
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			ok() {
				this.$Message.info('Clicked ok');
			},
			cancel() {
				this.$Message.info('Clicked cancel');
			}
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.img {
		width: 100%;
		height: 200px;
		display: block;
	}

	.top {
		margin-top: 200px;
	}

	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.de_cont {
			padding: 20px;
		}
		span {
			display: block;
			text-align: left;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #F6F6F6;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>



